<template>
	<div id="container">
	 <div id="header">
	 	<div id="title">小马宝莉</div>
	 	<div id="name">非凡公主</div>
	 	<div id="level" @click="reload">{{level}}</div>
	 </div>
	 <div id="description">拥有了友谊，才能使用魔法</div>
	 <div class="color-list" >
	 <div id="time" v-if="showTime"><p>{{timeNumber}}</p></div>
	 <div id="success" v-if="success">恭喜小仙女通关！</div>
      <div 
          class="color-item" 
          v-for="color in colors" v-dragging="{ item: color, list: colors, group: 'color' }"
          :key="color.index"
          :style="{backgroundPosition: color.position}"
          v-if="state"
      ></div>
  </div>
	</div>
</template>
<script>
export default{
	name: 'index',
	data () {
		return {
			 colors: [{
            index: 0,
            position: '0px 0px'
        }, {
            index: 8,
            position: '300px 300px'
        }, {
            index: 2,
             position: '300px 0px'
        }, {
            index: 4,
             position: '600px 600px'
        }, {
            index: 1,
             position: '600px 0px'
        }, {
            index: 3,
             position: '0px 600px'
        }, {
            index: 5,
             position: '300px 600px'
        }, {
            index: 7,
             position: '600px 300px'
        }, {
            index: 6,
             position: '0px 300px'
        }],
        state: false,
        showTime: false,
        timeNumber: 5,
        level: '第1关',
        success: false
		}
	},
	methods: {
		reload: function(){
			//alert('new')
			this.$router.go(0)
		}
	},
	mounted () {
	    this.$dragging.$on('dragged', ({ value }) => {
	      //console.log(value.item)
	      console.log(value.list)
	      for(let i = 0; i < 9;i++){
	      	console.log(value.list[i].index)
	      	if(value.list[i].index!=i)
	      		return 0
	      }
	      this.state=false
	      this.success=true
	      this.level = '重新开始'
	    })
	},
	created: function(){
		setTimeout(()=>{this.showTime=true},1000)
		setTimeout(()=>{this.timeNumber=4},2000)
		setTimeout(()=>{this.timeNumber=3},3000)
		setTimeout(()=>{this.timeNumber=2},4000)
		setTimeout(()=>{this.timeNumber=1},5000)
		setTimeout(()=>{this.showTime=false,this.state=true},6000)
	}
}
</script>
<style scoped>
.body{
	height: 100%;
}
#container{
	position: absolute;
	height: 100%;
	width: 100%;
	background-color: #EEEEE0;
}
.color-item{
	width: 300px;
	height: 300px;
	float: left;
	//margin: auto;
	border: solid 1px black;
	background-image: url('./../assets/level1.jpg');
	background-size: 900px 900px;
}
#header{
	width: 100%;
	font-size: 3rem;
	height: 160px;
}
#title{
	float: left;
	height: 120px;
	margin-left: 30px;
	margin-top: 20px;
	line-height: 120px;
	font-size: 4rem;
}
#level{
	float: right;
	width: 20%;
	background-color: #CDC9A5;
	padding: 20px;
	padding-top: 30px;
	padding-bottom: 30px;
	margin-top: 20px;
	color: white;
}
#name{
	float: right;
	width: 20%;
	background-color: #CDC9A5;
	padding: 20px;
	padding-top: 30px;
	padding-bottom: 30px;
	margin: 20px;
	color: white;
}
#description{
	width: 100%;
	height: 200px;
	font-size: 3rem;
	/float: left;
	text-align: left;
	margin-left: 20px;
	//background-color: red;
}
.color-list{

	width: 906px;
	height: 906px;
	margin: auto;
	background-image: url('./../assets/level1.jpg');
	background-size: 900px 900px;
	
}
#time p{
	position: absolute;
	left: 450px;
	top: 550px;
	font-size: 10rem;
}
#success{
	font-size: 4rem;
	position: absolute;
	top: 750px;
	left: 270px;
	color: #D02090;
}
</style>


